.mainpage {
       display:-webkit-flex;
       display:flex;
       min-height: 100vh;
       list-style-type:none;
       -webkit-flex-direction:column;
               flex-direction:column;
       background-color:grey;
       padding: 0px;
       box-shadow: 5px 10px 20px -20px rgba(85,172,238 ,1);
       }

.mainpage> :last-child {
	background-color:grey;
}
.mainbody {
	-webkit-flex:1 0;
	        flex:1 0;
	background-color:white;
}
.maintext {
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction:column;
	        flex-direction:column;
	-webkit-align-items:center;
	        align-items:center;
}

.header {
	display:-webkit-flex;
	display:flex;
       -webkit-flex-direction:row;
               flex-direction:row;
       list-style:none;
       height:40px;
       background-color:lightblue;
       border: 1px solid rgba(238,238,238 ,1);
       box-shadow: 5px 10px 20px -20px rgba(85,172,238 ,1);
       font-size:2em;
       margin:0;
       padding:20px;
       -webkit-justify-content:flex-start;
               justify-content:flex-start;
       -webkit-align-items:stretch;
               align-items:stretch;
        }
.header> :first-child {
	-webkit-flex-basis:300px;
	        flex-basis:300px;
	-webkit-flex: 0 0 20vw;
	        flex: 0 0 20vw;
	margin-right: 20vw;
	margin-left:10vw;
}
.header> :first-child .link {
	color:blue;
}





.header> :not(:first-child)  {
	-webkit-flex:  0 0 5em;
	        flex:  0 0 5em;
	margin-right:5px;
	-webkit-align-self:flex-start;
	        align-self:flex-start;
	cursor: pointer;
}
.header li:not(:first-child) :hover {
	  color: rgba(85,172,238 ,1);
}

.link {
      text-decoration: none;
    }


.header li {
	width:50px;
}


.footer {
	display:-webkit-flex;
	display:flex;
        min-height: 30px;
	padding: 20px;
	-webkit-flex-direction:row;
	        flex-direction:row;
	list-style:none;
	-webkit-justify-content:space-around;
	        justify-content:space-around;
	margin-left:10vw;
	margin-right:10vw;
	
}


